import React from "react";
import "./home.css";
import lantan1 from "./pic/lantan1.jpg";
import lantan2 from "./pic/lantan2.jpg";
import lantan3 from "./pic/lantan3.jpg";
import nihonbashi1 from "./pic/nihonbashi1.jpg";
import nihonbashi2 from "./pic/nihonbashi2.jpg";
import matinami1 from "./pic/matinami1.jpg";
import matinami2 from "./pic/matinami2.jpg";
import matinami3 from "./pic/matinami3.jpg";
import matinami5 from "./pic/matinami5.jpg";
import yoru1 from "./pic/yoru1.jpg";
import yoru2 from "./pic/yoru2.jpg";
//import { BrowserRouter, Route, Link } from "react-router-dom";
//import { withRouter } from "react-router";

const Tradition = () => {
  return (
    <div className="margin">
      <h1 className="tab-header">伝統物</h1>
      <p>歴史ある場所や物を紹介していきます</p>
      　　　 　　　
      <div className="introduce">
        <h2 className="intro-header">その1 ランタン</h2>
        <img src={lantan1} className="pic" />
        <img src={lantan2} className="pic" />
        <img src={lantan3} className="pic" />
        <p>
          ホイアンの魅力は何といってもこの華やかなランタンです。お店に、町中に、夜には川にも…様々な場所で
          このような綺麗なランタンが見られます。また、定期的にランタン祭りも行っており、
          その時には街の明かりが消され、ランタンの灯りだけになりとても幻想的な風景が見られます。
        </p>
      </div>
      <div className="introduce">
        <h2 className="intro-header">その2 日本橋</h2>
        <img src={nihonbashi1} className="pic" style={{ width: "40%" }} />
        <img src={nihonbashi2} className="pic" style={{ width: "40%" }} />
        <p>
          このサイトのトップの写真にもなっている日本橋は、名前からもわかるように
          1953年に日本人によってかけられた、ベトナムと日本の歴史を感じることができる橋です。
          チケットが必要にはなりますが、実際に橋を渡ることができ、多くの観光客でにぎわいます。
        </p>
      </div>
      <div className="introduce">
        <h2 className="intro-header">その3 街並み</h2>
        <img src={matinami1} className="pic" style={{ width: "40%" }} />
        <img src={matinami2} className="pic" style={{ height: "10%" }} />
        <img src={matinami3} className="pic" style={{ width: "40%" }} />
        <img src={matinami5} className="pic" style={{ width: "40%" }} />
        <p>
          ホイアンは町全体が世界遺産となっていて、とても美しく文化的な街並みを見ることができます。
          日本や中国、ポルトガルやオランダといった様々な国との国際貿易が盛んで、日本人街や中国人街が
          あったことなどから、その名残が見られます。黄色い壁がかわいいですね。また、夜になるとランタンがともされ、
          お店もライトアップされるのでまた違った街並みを楽しめます。
        </p>
        <img src={yoru1} className="pic" style={{ width: "40%" }} />
        <img src={yoru2} className="pic" style={{ width: "40%" }} />
      </div>
    </div>
  );
};

export default Tradition;
